* {
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
}
body {
  
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.popup {
  min-height: 5rem;
}

.row {
  display: flex;
  justify-content: center;
}

#data-container {
  background-image: linear-gradient(#777777 , #666666, #444444, #444444, #333333);
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-content: center;
  border: .2rem solid #666666;
  border-radius: .5rem;
}

.box > div:first-child {
  -webkit-text-stroke: .5px black;
}

.box > div:last-child {
  width: 100%;
  background-color: rgba(235, 235, 235, .8);
  border: 1px solid rgb(190, 190, 190);
  border-radius: .3rem;
  padding: .5rem 1rem;
  color: ghostwhite;
  box-shadow: inset 0 .2rem 0 rgb(0, 175, 175), 0 .2rem .5rem #000;
  text-align: center;
  transition: all 1.5s ease-in;

}

.box > div:last-child:hover {
  background-color: rgb(0, 175, 175);
  box-shadow: inset 0 .2rem 0 rgb(0, 96, 96), 0 .2rem .5rem #000;


} 
.linechart {
  padding: .8rem;
}

.linechart_path {
  stroke-width: 3;
  fill: none;
}

.linechart_axis {
  stroke: red;
}

.linechart_point {
  fill: #ffffff;
  stroke-width: 2;
}

.linechart_area {
  padding: .8rem;
  fill: rgb(0, 255, 81);
  stroke: none;
  opacity: .4;
}

.linechart_label {
  fill: black;
  font-weight: 700;
}

.hoverline {
  stroke: #cccccc;
  stroke-width: 1;
}
.hover {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  padding: 5px;
  border-radius: 4px;
  background-color: #2196F3;
  color: white;
}

.hover .price {
  font-weight: 700;
}

.infobox{
  width: 100%;
  max-width: 900px;
}


