.box {
  width: 27.77%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1rem 0;
}

.heading {
  font-size: 3.5rem;
  color: #2196F3;
}

.subtext {
  font-size: 1.2rem;
  color: #cccccc;
}
